import { onLoadingAction } from "@/redux/app/slice";
import { getSingleProductThunk } from "@/redux/seller/thunk";
import { AppDispatch } from "@/redux/store";
import { createSkuProductVaritant } from "@/utils/create-sku";
import { faWarning } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useRouter, useSearchParams } from "next/navigation";
import React, { useEffect } from "react";
import { useDispatch } from "react-redux";

export default function EditProduct() {
  const params = useSearchParams();
  const product_id = params.get("product_id");
  const router = useRouter();
  const dispatch = useDispatch<AppDispatch>();
  /**
   * replace page if product is is invalid or lenght of product id is less than 24 hexx
   */
  useEffect(() => {
    if (!product_id || product_id.length < 24) {
      const timer = setTimeout(() => {
        router.replace("/seller");
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [product_id, router]);

  /**
   * call single product api
   */
  useEffect(() => {
    if (product_id) {
      (async () => {
        dispatch(onLoadingAction(true));
        const result = await dispatch(getSingleProductThunk({ product_id }));
        if (result.payload || getSingleProductThunk.fulfilled.match(result)) {
          dispatch(onLoadingAction(false));
        }
      })();
    }
  });
  /**
   * render this ui before routing to dashboard page.
   */
  if (!product_id || product_id.length < 24) {
    return (
      <p className="uppercase">
        <span>
          <FontAwesomeIcon icon={faWarning} className="text-yellow-500" />
        </span>
        PRODUCT ID IS NOT FOUND OR WORNG OF FORM OBJECT ID. PLEAS DONT REWRITE
        URL ON BROWSER, OR CONTACT TO ADMIN IF USE CANNOT UNDERSTANT THIS ERROR!
      </p>
    );
  }
  return (
    <div className="p-2 bg-white">
      {/* edit information */}
      <div className="flex flex-col gap-3">
        {/* category */}
        <div>
          <label htmlFor=""></label>
        </div>
      </div>
    </div>
  );
}
